import { HttpClient, HttpClientModule } from '@angular/common/http';
import { ErrorHandler, NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { Camera } from '@ionic-native/camera';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { IonicStorageModule } from '@ionic/storage';
import { TranslateLoader, TranslateModule } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { EmailComposer } from '@ionic-native/email-composer';

import { Items } from '../providers/providers';
import { User } from '../providers/providers';
import { Contabilidad } from '../providers/providers';
import { Api } from '../providers/providers';
import { MyApp } from './app.component';
// The translate loader needs to know where to load i18n files
// in Ionic's static asset pipeline.
export function createTranslateLoader(http: HttpClient) {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}

@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: (createTranslateLoader),
        deps: [HttpClient]
      }
    }),
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/contact/contact.module.ngfactory#ContactPageModuleNgFactory', name: 'ContactPage', segment: 'contact', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/estadisticas/estadisticas.module.ngfactory#EstadisticasPageModuleNgFactory', name: 'EstadisticasPage', segment: 'estadisticas', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/item-create/item-create.module.ngfactory#ItemCreatePageModuleNgFactory', name: 'ItemCreatePage', segment: 'item-create', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/landing/landing.module.ngfactory#LandingPageModuleNgFactory', name: 'LandingPage', segment: 'landing', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/list-master/list-master.module.ngfactory#ListMasterPageModuleNgFactory', name: 'ListMasterPage', segment: 'list-master', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/listado-usuarios/listado-usuarios.module.ngfactory#ListadoUsuariosPageModuleNgFactory', name: 'ListadoUsuariosPage', segment: 'listado-usuarios', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/movimientos/movimientos.module.ngfactory#MovimientosPageModuleNgFactory', name: 'MovimientosPage', segment: 'movimientos', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/item-detail/item-detail.module.ngfactory#ItemDetailPageModuleNgFactory', name: 'ItemDetailPage', segment: 'item-detail', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/signup/signup.module.ngfactory#SignupPageModuleNgFactory', name: 'SignupPage', segment: 'signup', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/welcome/welcome.module.ngfactory#WelcomePageModuleNgFactory', name: 'WelcomePage', segment: 'welcome', priority: 'low', defaultHistory: [] }
  ]
}),
    IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    Api,
    Items,
    User,
    Contabilidad,
    Camera,
    SplashScreen,
    StatusBar,
    EmailComposer,
    // Keep this to enable Ionic's runtime error handling during development
    { provide: ErrorHandler, useClass: IonicErrorHandler }
  ]
})
export class AppModule { }



